import Vue from 'vue'
import moment from 'moment'

Vue.filter('formatPrice', (price, simbol) => {
    if(!parseInt(price)){
        return "";
    }
    return simbol + " " + (price.toFixed(2).replace('.',','));
}),

Vue.filter('formatDate', function(value) {
  if (value) {
    return moment(String(value)).format('DD/MM/YYYY HH:mm')
  }
})

