<template>
    <div id="mangaDetail" v-if="manga === undefined">
        <div class="templateContainer">    
          <b-img rounded="circle" v-bind="emptyImg" blank blank-color="hsla(212, 62%, 57%, 0.4)"></b-img>
          <div class="centered">Carregando</div>
        </div>
    </div>
    <div id="mangaDetail" v-else>
        <h1> Manga </h1>
        <b-row>
            <MangaForm @updated="editFeedback" :mangaEdit="savedManga" v-show="editFormShow"/>
        </b-row> 
        <b-row class="detailRow" align-v="center">
            <b-col cols=6 sm class='detailCol'>
                <b-img :src="manga.imagem" :alt="imgAlt" fluid
                style="min-width: 300px; max-width: 300px" class="detailImg"></b-img>
            </b-col>
            <b-col cols=6 sm class='detailCol'>                
                <b-card class="detailCard"  
                :title="manga.titulo"
                :sub-title="manga.subtitulo"               
                >      
                    <b-card-text class="center"> {{manga.autor}} </b-card-text>
                    
                    <b-card-text>
                        <div>{{manga.editora}}</div>
                        <div>Nº {{manga.edicao}}</div>                
                        <div>{{manga.preco | formatPrice('R$')}}</div>
                        <div> Favorito: {{manga.favorito ? "Sim" : "Não"}}</div>
                    </b-card-text>
                    <b-row>
                        <b-col>
                            <button @click="showEditForm" class="btn btn-primary detailButton">
                                Editar
                            </button>
                        </b-col>
                        <b-col>                            
                            <BackButton/>
                        </b-col>   
                    </b-row>                    
                </b-card>                           
                <b-alert fade class="successAlert"
                    :show="dismissCountDown"
                    dismissible
                    variant="success"                    
                    @dismissed="dismissCountDown=0"
                    @dismiss-count-down="countDownChanged"
                    >                   
                    Atualizado com Sucesso!                    
                </b-alert>
            </b-col>
        </b-row>
    </div>
</template>

<script>
import MangaForm from './MangaForm'
import BackButton from './BackButton'

import { mapGetters } from 'vuex'
export default {    
    name: 'MangaDetail',
    components: {
        MangaForm,
        BackButton,
        
        
    },    

    data: function(){
        return{
            editFormShow: false,
            savedManga: '',
            emptyImg: {width: 300, heigth: 300},
            imgAlt: 'Capa do Mangá',
            clicked: false,            
            dismissSecs: 5,
            dismissCountDown: 0,
            alertMessage: ''         
        }
    },

    created(){        
        this.savedManga = this.manga
    },

    methods:{
        showEditForm(){
            this.editFormShow = this.editFormShow ? false : true
        },

        editFeedback(){           
            this.showEditForm();
            this.alertMessage = 'Adicionado com sucesso!'
            this.showAlert();                     
        },        

        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },

        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },

    computed: {
        ...mapGetters(['getAllMangas']),        

        manga: function(){    
            return this.getAllMangas.filter(elem => elem.id == this.$route.params.id)[0]
        },
    },

}
</script>

<style>
   
    .detailRow{
        padding-top: 1%;
        padding-bottom: 5%;
    }    
    .detailCol{
        display: flex;
        display: -webkit-flex;        
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        min-width: fit-content;
    }
    .detailButton{
        margin: 5px;
    }

    .centered {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 1.5em;
    }
    .templateContainer {
        position: relative;
        text-align: center;
        margin: 5% 0;  
    }

    .detailCard{
        margin: 5% 0%;
    }

    
</style>