<template>
  <div id="formDiv">           
      <b-form @submit.prevent="operationValidation">
        <b-form-row>            
            <b-form-group class="col-6"
            id="tituloGroup"
            description="Insira o Título."
            label-align-sm="left"
            label="Título"
            label-for="titulo"            
            >
                <b-form-input id="tituloInput" v-model="manga.titulo" trim lazy required></b-form-input>
            </b-form-group>
        </b-form-row>
            
        <b-form-row>
            <b-form-group class="col-6"
            id="subtituloGroup"
            description="Insira o Subtítulo."
            label-align="left"
            label="Subtítulo"
            label-for="subtitulo"
            >
                <b-form-input id="subtituloInput" v-model="manga.subtitulo" trim lazy></b-form-input>
            </b-form-group>
        </b-form-row>
        <b-form-row>
                <b-form-group class="col-6"
                id="autorGroup"
                description="Insira o Autor."
                label-align="left"
                label="Autor"
                label-for="autor"
                >
                    <b-form-input id="autorInput" v-model="manga.autor" trim lazy required></b-form-input>
                </b-form-group>
        </b-form-row>
        <b-form-row class=""> 
            <b-form-group class="col-3"
            id="editoraGroup"
            description="Insira a editora."
            label-align="left"
            label="Editora"
            label-for="editora"
            >
                <b-form-input id="editoraInput" v-model="manga.editora" trim lazy required></b-form-input>
            </b-form-group>

            <b-form-group class="col-3"
            id="edicaoGroup"
            description="Insira a edição."
            label-align="left"
            label="Edição"
            label-for="edicao"
            >
                <b-form-input id="edicaoInput" type="number" v-model="manga.edicao" number trim lazy required></b-form-input>
            </b-form-group>
            
        </b-form-row>
        <b-form-row>
            <b-form-group class="col-6"
            id="imagemGroup"
            description="Insira o link da imagem da capa."
            label-align="left"
            label="Capa"
            label-for="imagem"
            >
                <b-form-input id="imagemInput" v-model="manga.imagem" trim lazy></b-form-input>
            </b-form-group>
        </b-form-row>    
        <b-form-row>
            <b-form-group class="col-3"
            id="precoGroup"
            description="Insira o preço."
            label-align="left"
            label="Preço"
            label-for="preco"
            >           
                <b-form-input id="precoInput" type="number" step="0.01" v-model="manga.preco" number trim lazy required></b-form-input>
            </b-form-group>            
            <b-form-group
            class="col-3 checkboxGroup"            
            >
                <b-row class="favRow">        
                    <b-form-checkbox class='checkbox' v-model="manga.favorito">Favorito?</b-form-checkbox>
                </b-row>
            </b-form-group>            
        </b-form-row>
        <b-form-row>
            <b-form-group class="col-6" id="submit">
                <b-row class="buttonsDiv col-6">
                   
                    <b-button v-if="typeof this.$route.params.id !== 'undefined'" type="submit" variant="primary">Salvar</b-button>
                    <b-button v-else type="submit" variant="primary">Adicionar</b-button>                    
                </b-row>
            </b-form-group>            
        </b-form-row>
        
    </b-form>    
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    name: 'mangaForm',
    created(){
        this.mock_manga()
        this.validate()          
    },

    props: {
        mangaEdit: Object
    },

    data() {
        return {
            editData: {countDown: 5, message: "Atualizado com Sucesso!"},

            manga: {
                                
                id: '',
                criado: '',
                titulo: "",
                subtitulo: "",
                autor:"",
                editora:"",
                edicao: 0,
                preco: 0,
                favorito: false,
                imagem: ""
            },

            placeholderImg: 'https://www.chicocharmoso.com.br/wp-content/uploads/woocommerce-placeholder-247x296.png'
           
       
        }
    },

    methods: {
        ...mapActions(['addManga', 'editManga']),
        
        getMangaMethod(id){
            return this.getManga(id)
        },

        validate(){
            if( typeof this.$route.params.id === 'string' ){
                this.manga = this.getMangaMethod(parseInt(this.$route.params.id))               
            }            
        },

        mock_manga(){            
            typeof this.mangaEdit === 'object' ? this.manga = this.mangaEdit : ''           
            this.manga.imagem === this.placeholderImg ? 
            this.manga.imagem = "" : ''
        },

        operationValidation(){            
            return typeof this.$route.params.id !== 'undefined' ? this.saveUpdatedManga() : this.submitManga()
        },

        saveUpdatedManga(){
            this.editManga(this.getMangaMethod(parseInt(this.$route.params.id)))
            
            this.$emit('updated')        
        },

        submitManga(){
            this.addManga(this.manga)                                 
            this.resetForm();
            this.$emit('added', 'gg')
                                  
        },

        resetForm(){
            this.manga = {
                id: '',
                titulo: "",
                subtitulo: "",
                autor:"",
                editora:"",
                edicao: 0,
                preco: 0,
                favorito: false,
                imagem: ""
            }
        }
    },

    computed:{           
        ...mapGetters(['getManga']),
    }

}
</script>

<style>
    #formDiv{
        display: flex;
        flex-flow: column nowrap;        
        align-content: center;
        width: 100%;
        margin: 1%;
    }

    .form-row{
        justify-content: center;
    }

    #submit{
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-end;       
    }



    .buttonsDiv{
        width: 100%;
        height: 100%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: flex-end;
        
    } 
    .bv-no-focus-ring{
        display: flex;
        width: 100%;
        height: 100%;
        flex-flow: column wrap;
        align-items: flex-start;
        align-content: flex-end;
    }
    .favRow{
        width: 100%;
        height: 100%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;        
        align-items: center;
    }

    .checkbox.custom-control.custom-checkbox{
        padding: 0px 8px;
    }


</style>