<template>
  <div id="collectionDetail">
    <b-table class="col-5" stacked :items="info" :fields="fields">
        <template v-slot:cell(total_gasto)="data">
            {{ data.item.total_gasto | formatPrice('R$') }}
        </template>
        <template v-slot:cell(maior_valor_gasto)="data">
            {{ data.item.maior_valor_gasto | formatPrice('R$') }}
        </template>
        <template v-slot:cell(menor_valor_gasto)="data">
            {{ data.item.menor_valor_gasto | formatPrice('R$') }}
        </template>
    </b-table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
    name: 'CollectionDetail',

    created(){
        this.mock_info();
    },

    data(){
        return{
            info: '',

            fields: [
                { key: 'counter', label: 'Mangás na Coleção'},
                'favoritos',            
                { key: 'total_gasto', label: 'Total Gasto' },
                { key: 'maior_valor_gasto', label: "Maior Valor Gasto" },
                { key: 'menor_valor_gasto', label: "Menor Valor Gasto" }
                
            ]
        }
    },  

    methods:{
        mock_info(){

            this.info = [{ 
                counter: this.mangasQtd,
                favoritos: this.favoritosQtd,
                total_gasto: this.total_gasto,                          
                maior_valor_gasto: this.max_gasto,
                menor_valor_gasto: this.min_gasto
                }]
            
        }
    },

    computed: {
        ...mapGetters(['getAllMangas', 'getAllFavorites']),

        mangasQtd(){
            return this.getAllMangas.length
        },

        favoritosQtd(){            
            return this.getAllFavorites.length
        },

        total_gasto(){
            return this.getAllMangas.map(manga => manga.preco)
            .reduce((total, preco) => total += preco, 0)
        },

        max_gasto(){
            return Math.max(...this.getAllMangas.map(manga => manga.preco))
        },

        min_gasto(){
            return Math.min(...this.getAllMangas.map(manga => manga.preco))
        }
    }
}
</script>

<style>
    #collectionDetail{
        margin: 1%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;

    }
</style>