import axios from 'axios'

const state = {
    mangas: [],       
};

const getters = {
    getAllMangas: state => state.mangas,
    getAllFavorites: state => state.mangas.filter(manga => manga.favorito === true),
    getManga: (state) => (id) => state.mangas.filter(elem => elem.id === id)[0]    
    
};

const mutations = {
    setMangas: (state, mangas) => state.mangas = mangas,

    removeManga: (state, id) => state.mangas = state.mangas.filter(m => m.id !== id),

    insertManga: (state, manga) => state.mangas.push(manga),

    updateManga: (state, manga) => state.mangas.filter(m => m.id === manga.id)[0] = manga,
};

const actions = {    
    
    mockStore: ({commit}) => {
        axios.get('../static/mangas.json').then((response) => {            
            commit('setMangas', response.data.mangas)
        })
    },

    deleteManga: ({commit}, id) => {
        commit('removeManga', id)
    },
    
    addManga: ({commit}, manga) => {
        let list = state.mangas
        manga.id = list.map(manga => manga.id).sort((a,b) => a-b)[list.length - 1] + 1;
        manga.criado = new Date().toJSON()      
            
        if (manga.imagem === '' || 
            manga.imagem.slice(0,4).toLowerCase() !== 'http' &&
            manga.imagem.slice(0,3).toLowerCase() !== 'www') { 
            manga.imagem = 'https://www.chicocharmoso.com.br/wp-content/uploads/woocommerce-placeholder-247x296.png'
        }
        commit('insertManga', manga)
    },

    editManga({commit}, manga) {
        commit('updateManga', manga);
    }
};

export default {
    state,
    actions,
    getters,
    mutations
}