<template>
  <div class="home">    
    <h3>Início</h3>
    <p>Bem Vindo!</p>
    <p> Confira os últimos mangás adicionados</p>
    <b-row id="lastAddedRow" class="deckRow">
    <CardDeck class="deck" msg="Últimos adicionados" :mangas="lastAdded"></CardDeck>
    <template  v-if="lastAdded.length === 0">
        <div class="templateContainer">    
          <b-img rounded="circle" v-bind="emptyImg" blank blank-color="hsla(212, 62%, 57%, 0.4)"></b-img>
          <div class="centered">Insira seus Mangás</div>
        </div>
    </template>   
    </b-row>
    <b-row id="lastFavRow" class="deckRow">
    <CardDeck class="deck" msg="Últimos favoritos" :mangas="lastFavorites"></CardDeck>
    <template  v-if="lastFavorites.length === 0">
        <div class="templateContainer">    
          <b-img rounded="circle" v-bind="emptyImg" blank blank-color="hsla(212, 62%, 57%, 0.4)"></b-img>
          <div class="centered">Favorite seus Mangás</div>
        </div>
    </template>
    </b-row>
  </div>
</template>

<script>
// @ is an alias to /src
import CardDeck from '../components/CardDeck.vue'
import { mapGetters} from "vuex";
export default {
  name: 'Home',
  components: {    
    CardDeck
  },

  data(){
    return {  
      emptyImg: {width: 300, heigth: 300}   
    }
  },

  methods:{ 

    getLastFour(mangasList){
      if(mangasList.length > 4)
        return mangasList.slice(mangasList.length-4,mangasList.length).reverse();
      else{
        return mangasList.reverse();
      }
    },
    
  },

  computed: {

    ...mapGetters([
      'getAllMangas',
      'getAllFavorites' 
    ]),

    lastAdded: function(){
      return this.getAllMangas.length > 4 ?
      this.getAllMangas.slice(this.getAllMangas.length - 4, this.getAllMangas.length).reverse() :
      this.getAllMangas.slice().reverse()
    },

    lastFavorites: function(){      
      return this.getAllFavorites.length > 4 ?
      this.getAllFavorites.slice(this.getAllFavorites.length - 4, this.getAllFavorites.length).reverse() :
      this.getAllFavorites.slice().reverse()
    }
  }
}
</script>

